
.contenido-servicios{
    text-align: center;
    h3{
        font-weight: $delgada;
        font-size: $separacion;
    }
}
.galeria-servicios{
    padding-right: 5r;
    list-style: none;
    margin-top: $separacion;

    @include smartwatch{
        img{
            width: 26rem;
            height: 26rem;
            border-radius: 50%;
            
        }
        @include grid (1,$separacion)
    }
    @include telefono{
        img{
            width: 26rem;
            height: 26rem;
            border-radius: 50%;
            padding: $separacion auto;
        }
        @include grid (2,$separacion)
    }
    @include tablet { // Tiene un bloque que varia
        img{
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
        }
        @include grid (2,$separacion)
    }
    @include desktop {
        img{
            width: 45rem;
            height: 45rem;
            border-radius: 50%;
        }
        @include grid(2, $separacion)
    }
}
.img-1{
    background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/tratamiento-capilar-sevilla_hgfsfk.jpg');
    background-repeat: no-repeat;
    border-radius: 50%;
    background-size: cover;
    align-items: center;
    @include smartwatch {
        padding-top: 2rem;
        width: 26rem;
        height: 26rem;
        border-radius: 50%;
        padding: $separacion auto;
        .div-size{
            text-align: center;
            color: $blanco;
            width: 26rem;
            height: 26rem;
            p{
                margin-top: 2rem;
                text-align: center;
                color: $blanco;
                font-size: 1.3rem;
                padding: 0 2rem;
            }
        
        }
    @include telefono {
        padding-top: 2rem;
        width: 26rem;
        height: 26rem;
        border-radius: 50%;
        padding: $separacion auto;
        .div-size{
            color: $blanco;
            width: 26rem;
            height: 26rem;
            p{
                margin-top: 2rem;
                text-align: center;
                color: $blanco;
                font-size: 1.3rem;
                padding: 0 2rem;
        }
            
        }
        }
    @include tablet {
        padding-top: 2rem;
        width: 30rem;
        height: 30rem;
        border-radius: 50%;
        padding: $separacion auto;
        .div-size{
            color: $blanco;
            width: 30rem;
            height: 30rem;
            h5{
                font-size: 2rem;
            }
            p{
                margin-top: 2rem;
                text-align: center;
                color: $blanco;
                font-size: 1.3rem;
                padding: 0 2rem;
            }
                
        }
        }
    @include desktop {
        margin-top: 2rem;
        width: 50rem;
        height: 50rem;
        border-radius: 50%;
        padding: $separacion auto;
        .div-size{
            padding-top: 10rem;
            color: $blanco;
            width: 50rem;
            height: 50rem;
            H5{
                font-size: 3rem;
            }
            p{
                margin-top: 10rem;
                text-align: center;
                color: $blanco;
                font-size: 2rem;
                padding: 0 2rem;
        }
                        
            
        }
    }
    }
}

