.opacar{
    background: rgb(254,187,187); /* Old browsers */
    background: -moz-linear-gradient(top,  rgba(254,187,187,1) 0%, rgba(254,144,144,1) 59%, rgba(254,144,144,1) 59%, rgba(254,144,144,1) 102%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(254,187,187,1) 0%,rgba(254,144,144,1) 59%,rgba(254,144,144,1) 59%,rgba(254,144,144,1) 102%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(254,187,187,1) 0%,rgba(254,144,144,1) 59%,rgba(254,144,144,1) 59%,rgba(254,144,144,1) 102%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#febbbb', endColorstr='#fe9090',GradientType=0 ); /* IE6-9 */

    transition: all 2s;
}
.opacar:hover{
    background: black;
}
.body-Color{
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#e0f3fa+0,b8e2f6+77,b6dffd+100 */
        background: rgb(224,243,250); /* Old browsers */
        background: -moz-linear-gradient(top,  rgba(224,243,250,1) 0%, rgba(184,226,246,1) 77%, rgba(182,223,253,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  rgba(224,243,250,1) 0%,rgba(184,226,246,1) 77%,rgba(182,223,253,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  rgba(224,243,250,1) 0%,rgba(184,226,246,1) 77%,rgba(182,223,253,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e0f3fa', endColorstr='#b6dffd',GradientType=0 ); /* IE6-9 */
    
    color:$blanco;
    transition: all 3s;
}
.body-Color:hover{
    background: rgb(117, 115, 115);
}