html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  height: 100%;
  margin: 0;
}

p {
  font-size: 2rem;
  color: #000000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

p {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
  margin-top: 0;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-repeat: no-repeat;
  background-position: bottom;
  background-attachment: fixed;
  background-image: url("https://res.cloudinary.com/dpikphrtb/image/upload/v1672619157/fondo4_ptrlpz.jpg");
  background-size: cover;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.header .contenido-logo {
  padding-top: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: #FFF;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.5);
}
@media (min-width: 250px) {
  .header .contenido-logo {
    margin: 0;
    height: 65rem;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .header .contenido-logo {
    width: 100%;
    height: 50rem;
  }
}
@media (min-width: 468px) {
  .header .contenido-logo {
    width: 100%;
    height: 60rem;
  }
}
@media (min-width: 1200px) {
  .header .contenido-logo {
    width: 100%;
    height: 80rem;
  }
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 4rem;
  background-color: #eeeeee;
  padding: 2rem;
  padding-top: 0;
}
@media (min-width: 250px) {
  .navegacion-principal {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.5rem;
    border-bottom: 1px solid #eeeeee;
  }
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0 auto;
    row-gap: 2rem;
    font-size: 3rem;
  }
}
@media (min-width: 468px) {
  .navegacion-principal {
    display: flex;
    flex-direction: inherit;
    justify-content: space-evenly;
    font-size: 4rem;
  }
}
@media (min-width: 1200px) {
  .navegacion-principal {
    display: flex;
    flex-direction: inherit;
    justify-content: space-evenly;
    font-size: 4rem;
  }
}
.navegacion-principal .link {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 300;
}
.navegacion-principal .link:hover {
  cursor: pointer;
  color: rgb(255, 0, 0);
}

.logo-small {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #eeeeee;
  border-bottom: 0.1px solid #DCDCDC;
}
.logo-small #image-small {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
@media (min-width: 250px) {
  .logo-small #image-small {
    width: 28%;
  }
}
@media (min-width: 768px) {
  .logo-small #image-small {
    width: 20%;
  }
}
@media (min-width: 468px) {
  .logo-small #image-small {
    width: 15%;
  }
}
@media (min-width: 1200px) {
  .logo-small #image-small {
    width: 10%;
  }
}

.nosotros {
  background-repeat: no-repeat;
  background-position: bottom;
  background-attachment: fixed;
  background-image: url("https://res.cloudinary.com/dpikphrtb/image/upload/v1672604491/fondo1_qwuqli.jpg");
  background-size: cover;
  font-weight: 300;
  color: #FFF;
}
.nosotros .contenido-nosotros {
  overflow: hidden;
  text-align: center;
  font-family: "Open Sans", sans-serif;
}
.nosotros .contenido-nosotros h3 {
  font-weight: 300;
  font-size: 5rem;
}
.nosotros .contenido-nosotros p {
  color: #FFF;
  font-size: 2rem;
}
.nosotros .contenido-nosotros #nosotros-selector {
  border: 1px solid #FFF;
  font-weight: 300;
  font-size: 3rem;
}

.contenido-servicios {
  text-align: center;
}
.contenido-servicios h3 {
  font-weight: 300;
  font-size: 5rem;
}

.galeria-servicios {
  padding-right: 5r;
  list-style: none;
  margin-top: 5rem;
}
@media (min-width: 250px) {
  .galeria-servicios {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 5rem;
  }
  .galeria-servicios img {
    width: 26rem;
    height: 26rem;
    border-radius: 50%;
  }
}
@media (min-width: 768px) {
  .galeria-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .galeria-servicios img {
    width: 26rem;
    height: 26rem;
    border-radius: 50%;
    padding: 5rem auto;
  }
}
@media (min-width: 468px) {
  .galeria-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .galeria-servicios img {
    width: 30rem;
    height: 30rem;
    border-radius: 50%;
  }
}
@media (min-width: 1200px) {
  .galeria-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .galeria-servicios img {
    width: 45rem;
    height: 45rem;
    border-radius: 50%;
  }
}

